  return <><_components.p>{"¬†"}</_components.p>{"\n"}<_components.h1>{"‚ùì Frequently Asked Questions (FAQ)"}</_components.h1>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[0].id}>{toc[0].value}</_components.h2>{"\n"}<_components.p>{"Yaci Store is a modular and extensible chain indexer for the Cardano blockchain. It extracts and indexes comprehensive on-chain data into a PostgreSQL database and exposes high-performance RESTful APIs. Designed for developers, it supports fast queries, easy customization, and seamless integration with applications that need insight into transaction flows, accounts, rewards, and governance."}</_components.p>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[1].id}>{toc[1].value}</_components.h2>{"\n"}<_components.p>{"Yaci Store stands out by combining "}<_components.strong>{"performance, modularity, and completeness"}</_components.strong>{":"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"‚úÖ "}<_components.strong>{"First full-featured indexer after DB Sync"}</_components.strong>{" that tracks "}<_components.strong>{"rewards"}</_components.strong>{", "}<_components.strong>{"epoch stake"}</_components.strong>{", and "}<_components.strong>{"governance state"}</_components.strong></_components.li>{"\n"}<_components.li>{"üß© "}<_components.strong>{"Modular plugin architecture"}</_components.strong>{": Activate only the stores you need"}</_components.li>{"\n"}<_components.li>{"üöÄ "}<_components.strong>{"High sync performance"}</_components.strong>{" with optional data pruning"}</_components.li>{"\n"}<_components.li>{"üõ†Ô∏è "}<_components.strong>{"Built-in REST APIs with Swagger UI"}</_components.strong>{"‚Äîno GraphQL or extra layers required"}</_components.li>{"\n"}<_components.li>{"ü™∂ "}<_components.strong>{"Lighter footprint"}</_components.strong>{" compared to DB Sync and some Haskell-based solutions"}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[2].id}>{toc[2].value}</_components.h2>{"\n"}<_components.ul>{"\n"}<_components.li>{"üì¶ "}<_components.strong>{"Pluggable Stores"}</_components.strong>{": Enable/disable data stores like transactions, assets, blocks, rewards, and governance"}</_components.li>{"\n"}<_components.li>{"üß≠ "}<_components.strong>{"Governance & Reward Indexing"}</_components.strong>{": Access to proposal statuses, DRep tracking, delegation, and reward events"}</_components.li>{"\n"}<_components.li>{"‚öôÔ∏è "}<_components.strong>{"Pruning Support"}</_components.strong>{": Reduce disk usage by trimming old UTxO and transaction data"}</_components.li>{"\n"}<_components.li>{"üêò "}<_components.strong>{"PostgreSQL-native"}</_components.strong>{": All indexed data is stored in a normalized SQL schema"}</_components.li>{"\n"}<_components.li>{"üß™ "}<_components.strong>{"RESTful API"}</_components.strong>{": Swagger auto-documented endpoints, easy to integrate"}</_components.li>{"\n"}<_components.li>{"üîÑ "}<_components.strong>{"Standalone or Docker"}</_components.strong>{": Supports multiple deployment models"}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[3].id}>{toc[3].value}</_components.h2>{"\n"}<_components.ul>{"\n"}<_components.li>{"‚ö° "}<_components.strong>{"Fast Full-Chain Syncing"}</_components.strong>{" (~24h under tested conditions)"}</_components.li>{"\n"}<_components.li>{"üß∞ "}<_components.strong>{"Customizable Store Composition"}</_components.strong>{" for lighter deployments"}</_components.li>{"\n"}<_components.li>{"üíæ "}<_components.strong>{"Disk Efficiency with Pruning Options"}</_components.strong></_components.li>{"\n"}<_components.li>{"üî¨ "}<_components.strong>{"Detailed Governance & Ledger State Tracking"}</_components.strong></_components.li>{"\n"}<_components.li>{"üîç "}<_components.strong>{"Transparent and queryable via API without extra infrastructure"}</_components.strong></_components.li>{"\n"}<_components.li>{"üë∑ "}<_components.strong>{"Suited for both production and experimental usage"}</_components.strong></_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[4].id}>{toc[4].value}</_components.h2>{"\n"}<_components.p>{"Here are results from our most recent benchmarks:"}</_components.p>{"\n"}<_components.h3 id={toc[5].id}>{toc[5].value}</_components.h3>{"\n"}<_components.ul>{"\n"}<_components.li>{"VM: 64 GB RAM, 32 cores"}</_components.li>{"\n"}<_components.li>{"Cardano Node: IOG public relay (remote)"}</_components.li>{"\n"}<_components.li>{"Storage: Docker Compose, PostgreSQL with "}<_components.code>{"shm_size=2g"}</_components.code></_components.li>{"\n"}</_components.ul>{"\n"}<_components.blockquote>{"\n"}<_components.p>{"‚ö†Ô∏è "}<_components.strong>{"Note"}</_components.strong>{": Yaci Store does "}<_components.em>{"not"}</_components.em>{" require 64 GB RAM. A VM with "}<_components.strong>{"8‚Äì10 GB RAM"}</_components.strong>{" and "}<_components.strong>{"6‚Äì8 cores"}</_components.strong>{" is typically sufficient."}</_components.p>{"\n"}</_components.blockquote>{"\n"}<_components.h3 id={toc[6].id}>{toc[6].value}</_components.h3>{"\n"}<_components.table><_components.thead><_components.tr><_components.th>{"Configuration"}</_components.th><_components.th>{"Sync Time"}</_components.th><_components.th>{"Disk Usage"}</_components.th></_components.tr></_components.thead><_components.tbody><_components.tr><_components.td>{"All on-chain data (ledger state off)"}</_components.td><_components.td>{"~24h"}</_components.td><_components.td>{"689 GB"}</_components.td></_components.tr><_components.tr><_components.td>{"With transaction witness enabled"}</_components.td><_components.td>{"~24h"}</_components.td><_components.td>{"~790 GB"}</_components.td></_components.tr><_components.tr><_components.td>{"With UTXO pruning enabled"}</_components.td><_components.td>{"~24h"}</_components.td><_components.td>{"338 GB"}</_components.td></_components.tr></_components.tbody></_components.table>{"\n"}<_components.blockquote>{"\n"}<_components.p>{"Transaction pruning was disabled during these tests but is supported."}<_components.br />{"\n"}{"Additional benchmarks (including with ledger state enabled) are planned."}</_components.p>{"\n"}</_components.blockquote>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[7].id}>{toc[7].value}</_components.h2>{"\n"}<_components.p>{"Yaci Store is the ideal choice when you need:"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"A fast alternative to DB Sync with "}<_components.strong>{"lower resource requirements"}</_components.strong></_components.li>{"\n"}<_components.li>{"Full access to "}<_components.strong>{"on-chain governance"}</_components.strong>{", "}<_components.strong>{"DReps"}</_components.strong>{", "}<_components.strong>{"proposal history"}</_components.strong>{", and "}<_components.strong>{"staking data"}</_components.strong></_components.li>{"\n"}<_components.li>{"Fine-grained "}<_components.strong>{"modularity and control"}</_components.strong>{" over what data to index"}</_components.li>{"\n"}<_components.li>{"A "}<_components.strong>{"PostgreSQL-backed indexer"}</_components.strong>{" with a developer-friendly API layer"}</_components.li>{"\n"}<_components.li>{"An easy way to "}<_components.strong>{"prune"}</_components.strong>{" unnecessary data and keep infrastructure lean"}</_components.li>{"\n"}<_components.li>{"A solution that doesn‚Äôt require setting up complex GraphQL layers or Kafka pipelines"}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[8].id}>{toc[8].value}</_components.h2>{"\n"}<_components.table><_components.thead><_components.tr><_components.th><_components.strong>{"Solution"}</_components.strong></_components.th><_components.th><_components.strong>{"Language"}</_components.strong></_components.th><_components.th><_components.strong>{"Key Characteristics"}</_components.strong></_components.th></_components.tr></_components.thead><_components.tbody><_components.tr><_components.td><_components.strong>{"Kupo"}</_components.strong></_components.td><_components.td>{"Rust"}</_components.td><_components.td>{"Kupo focuses exclusively on UTxO indexing and offers a blazing-fast HTTP API to retrieve UTxOs by address, stake address, or policy ID. It is lightweight but lacks broader chain state support (e.g. governance, rewards)."}</_components.td></_components.tr><_components.tr><_components.td><_components.strong>{"Yaci Store"}</_components.strong></_components.td><_components.td>{"Java"}</_components.td><_components.td>{"Full ledger state indexer with REST APIs, governance/rewards support, pruning, and multiple storage backends."}</_components.td></_components.tr><_components.tr><_components.td><_components.strong>{"Oura"}</_components.strong></_components.td><_components.td>{"Rust"}</_components.td><_components.td>{"Event-based filter pipeline; no built-in storage ‚Äî requires external sinks like Kafka or Elastic."}</_components.td></_components.tr><_components.tr><_components.td><_components.strong>{"Adder"}</_components.strong></_components.td><_components.td>{"Go"}</_components.td><_components.td>{"Event emitter with filters; no built-in persistence ‚Äî needs backend integration."}</_components.td></_components.tr><_components.tr><_components.td><_components.strong>{"Scrolls"}</_components.strong></_components.td><_components.td>{"Rust"}</_components.td><_components.td>{"In-memory Redis-based aggregator; best for real-time metrics, not full-chain history."}</_components.td></_components.tr><_components.tr><_components.td><_components.strong>{"Carp"}</_components.strong></_components.td><_components.td>{"Rust"}</_components.td><_components.td>{"Plugin-based indexer built on Oura; stores in PostgreSQL using custom or prebuilt tasks."}</_components.td></_components.tr><_components.tr><_components.td><_components.strong>{"Cardano DB-Sync"}</_components.strong></_components.td><_components.td>{"Haskell"}</_components.td><_components.td>{"Most complete indexer; heavy on resources and sync time; full PostgreSQL schema."}</_components.td></_components.tr></_components.tbody></_components.table>{"\n"}<_components.hr />{"\n"}<_components.h2 id={toc[9].id}>{toc[9].value}</_components.h2>{"\n"}<_components.p>{"Choose "}<_components.strong>{"Yaci Store"}</_components.strong>{" if you:"}</_components.p>{"\n"}<_components.ul>{"\n"}<_components.li>{"Need rich governance and rewards data with ledger-state accuracy."}</_components.li>{"\n"}<_components.li>{"Want a pluggable, standalone indexer with no external dependencies."}</_components.li>{"\n"}<_components.li>{"Prefer REST APIs over writing custom sinks."}</_components.li>{"\n"}<_components.li>{"Need fine-grained pruning for space efficiency."}</_components.li>{"\n"}<_components.li>{"Are building systems that rely on stake snapshots, constitutional state, or DRep data."}</_components.li>{"\n"}</_components.ul>{"\n"}<_components.p>{"Use alternatives like "}<_components.strong>{"Oura"}</_components.strong>{" or "}<_components.strong>{"Carp"}</_components.strong>{" if you‚Äôre designing event-based pipelines, need quick filters, or already use Kafka/Redis for real-time streaming."}</_components.p></>;
